* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	z-index: 100;
	&::after {
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		z-index: 100;
	}
	&::before {
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		z-index: 100;
	}
}

:root {
	--font-size: 16px;
	--font-size-var1: clamp(18px, 1.85vw, 24px);
	--font-size-var2: clamp(12px, 1.23vw, 16px);
	--color-bg: #12b992;
	--color-white: #ffffff;
	--color-dark: #484848;
	--color-green: #12b992;
	--color-orange: #f8c227;
	--color-blue: #225980;
	--color-gray: #413e5e;
}
a {
	text-decoration: none !important;
	color: inherit;
}

html {
	background-color: var(--color-bg);
	margin-right: 0 !important;
}
body {
	font-family: var(--font-main);
	font-style: normal;
	font-weight: normal;
	font-size: var(--font-size-var2);
	color: var(--color-dark);
}
.wrapper {
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: start;
	-ms-flex-pack: start;
	justify-content: flex-start;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	min-height: 100%;
}
img {
	max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
	-webkit-font-feature-settings: 'ordn' on;
	font-feature-settings: 'ordn' on;
	-webkit-font-feature-settings: 'pnum' on, 'lnum' on;
	font-feature-settings: 'pnum' on, 'lnum' on;
}
h1 {
	font-weight: 800;
	font-size: var(--font-size-var1);
	line-height: 130%;
	color: var(--color-blue);
	text-transform: uppercase;
}
h2 {
	font-weight: 800;
	font-size: var(--font-size-var1);
	line-height: 150%;
	color: var(--color-blue);
	text-transform: uppercase;
}
h3 {
	font-weight: bold;
	font-size: var(--font-size-var2);
	line-height: 129%;
	color: var(--color-dark);
}
sup {
	font-size: 0.6em;
}
sub {
	font-size: 0.6em;
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.section {
	max-width: 100%;
	width: 100%;
	position: relative;
	&__content {
		max-width: 1320px;
		margin: 0 auto;
		padding: 0 20px;
	}
}
._1 {
	.section__content {
		padding: 20px 0;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		.rating {
			display: flex;
			align-items: flex-end;
			font-size: 22px;
			line-height: 1;
			padding-bottom: 20px;
			letter-spacing: 4px;
		}
		.rating__body {
			position: relative;
		}
		.rating__body::before {
			content: '★★★★★';
			display: block;
			color: #fff;
		}
		.rating__active {
			position: absolute;
			width: 87%;
			height: 100%;
			top: 0;
			left: 0;
			overflow: hidden;
		}
		.rating__active::before {
			content: '★★★★★';
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			color: #f5d84f;
		}
		.rating__items {
			display: flex;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
		.rating__item {
			flex: 0 0 20%;
			height: 100%;
			opacity: 0;
		}
		.rating__value {
			font-size: 50%;
			line-height: 1;
			padding: 0 0 0 10px;
		}
	}
}
